import { useEffect, useRef } from "react";
import io from "socket.io-client";

export default function Viewer() {
  const videoRef = useRef(null);
  const socket = useRef(null);
  const pc = useRef(null);

  useEffect(() => {
    socket.current = io(process.env.REACT_APP_SERVER_URL); // your server URL
    pc.current = new RTCPeerConnection();

    // Handle offer from Python client
    socket.current.on("offer", async (data) => {
      console.log("Received offer");
      await pc.current.setRemoteDescription(new RTCSessionDescription(data));
      const answer = await pc.current.createAnswer();
      await pc.current.setLocalDescription(answer);
      socket.current.emit("answer", pc.current.localDescription);
    });

    // When remote media stream arrives
    pc.current.ontrack = (event) => {
      console.log("Track received");
      if (videoRef.current) {
        videoRef.current.srcObject = event.streams[0];
      }
    };

    // ICE candidate from server
    socket.current.on("ice-candidate", (candidate) => {
      pc.current.addIceCandidate(new RTCIceCandidate(candidate));
    });

    // Send ICE candidates to server
    pc.current.onicecandidate = (event) => {
      if (event.candidate) {
        socket.current.emit("ice-candidate", event.candidate);
      }
    };

    return () => {
      // Clean up on unmount
      if (pc.current) {
        pc.current.close();
      }
      if (socket.current) {
        socket.current.disconnect();
      }
    };
  }, []);

  return (
    <div style={{ padding: "1rem" }}>
      <h2>Live Stream</h2>
      <video
        ref={videoRef}
        autoPlay
        playsInline
        controls={false}
        style={{ width: "100%", backgroundColor: "#000" }}
      />
    </div>
  );
}
